import { CartContext } from './context/CartContext';
import { NotificationContext } from './context/NotificationContext';

import { BrowserRouter, Routes, Route } from 'react-router-dom';

import NavBar from './components/nav/NavBar';
import Footer from './components/footer/Footer';

import ItemListContainer from './components/body/itemListContainer/ItemListContainer';
import ItemDetailContainer from './components/body/itemDetailContainer/ItemDetailContainer';
import CartViewContainer from './components/body/cartViewContainer/CartViewContainer';
import CheckoutContainer from './components/body/checkoutContainer/CheckoutContainer';
import Notification from './components/notification/Notification';
import DashBoardContainer from './components/body/dashBoard/DashBoardContainer';

function App() {
    return (
        <div className="App">
            <NotificationContext>
                <CartContext>
                    <BrowserRouter>
                        <NavBar />
                        <Notification />
                        <Routes>
                            <Route path="https://monderks.github.io/studiogoldney_clifton_goldney/" element={<ItemListContainer />}></Route>
                            <Route path="https://monderks.github.io/studiogoldney_clifton_goldney/category/:categoryId" element={<ItemListContainer />}></Route>
                            <Route path="https://monderks.github.io/studiogoldney_clifton_goldney/item/:paramId" element={<ItemDetailContainer />}></Route>
                            <Route path="https://monderks.github.io/studiogoldney_clifton_goldney/cart" element={<CartViewContainer />}></Route>
                            <Route path="https://monderks.github.io/studiogoldney_clifton_goldney/checkout" element={<CheckoutContainer />}></Route>
                            <Route path="https://monderks.github.io/studiogoldney_clifton_goldney/dashboard" element={<DashBoardContainer />}></Route>
                        </Routes>
                        <Footer />
                    </BrowserRouter>
                </CartContext>
            </NotificationContext>
        </div>
    );
}

export default App;
